@import '../../styles/style_utils';

.no-results {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
  gap: 30px;
  padding-top: 30px;

  @include on-tablet {
    padding-top: 60px;
  }

  &__paws {
    width: 100%;
    min-height: 200px;
    background-image: url('../../img/paws2.png');
    background-position: center;
  }

  &__title {
    text-align: center;
  }
}
