@import '../../styles/style_utils';

.pets-list {
  @include page-grid;

  grid-template-columns: 1fr;
  justify-items: center;

  @include on-tablet {
    grid-template-columns: repeat(2, 1fr);

    &--favorites {
      grid-template-columns: repeat(3, 1fr);
    }
  }

  @include on-desktop {
    grid-template-columns: repeat(3, 1fr);

    &--favorites {
      grid-template-columns: repeat(4, 1fr);
    }
  }
}
