@import '../../styles/style_utils';

.loader-overlay {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background: $modal-bg;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}

.loader-spinner {
  border: 8px solid #f3f3f3;
  border-top: 8px solid $blue-500;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}
